<template>
  <div class="queue-progress-container" :class="{ 'called-background': status === 'called' }">
    <!-- 顶部进度条 -->
    <div class="progress-container">
      <div class="progress-bar">
        <img :src="require(`../assets/${getProgressImage()}`)" alt="进度条" class="progress-img" />
      </div>
    </div>

    <!-- 返回按钮 -->
    <div class="back-button" @click="goBack">  
      <img src="../assets/back.png" alt="返回" class="back-img" />
    </div>

    <!-- 排队进度标题 -->
    <div class="queue-title">排队进度 {{ examType }}</div>

    <!-- 排队人员图标 -->
    <div class="queue-icons">
      <template v-for="(person, index) in 7" :key="index">
        <div class="person-icon">
          <img 
            v-if="(examType === '内科检查' && index === 2 && status === 'waiting') || 
                 (examType === '心电图' && index === 4 && status === 'waiting') || 
                 ((examType === '内科检查' || examType === '心电图') && index === 0 && status === 'called')" 
            :src="require(`../assets/${status === 'waiting' ? 'person-red.png' : 'person-green.png'}`)" 
            class="person-img" 
          />
          <img 
            v-else
            src="../assets/person.png" 
            class="person-img" 
          />
        </div>
      </template>
    </div>

    <!-- 排队信息 -->
<div v-if="status === 'waiting'" class="queue-info-text">
  <div class="queue-position">前方仅剩<span class="highlight-number">{{ examType === '内科检查' ? '2' : '6' }}</span>位等待</div>
  <div class="queue-notice">请准备好，即将轮到您</div>
</div>

    <!-- 排队信息 -->
    <div v-if="status === 'waiting'" class="info-container">
      <div class="waiting-info">
        <div class="date-time">3月19日 星期三 {{ examType === '内科检查' ? '10:12:22' : '10:12:22' }}</div>
        <div class="waiting-time-row">
          <img src="../assets/clock-icon.png" class="info-icon" />
          <div class="clock-text">预计等待时间为{{ examType === '内科检查' ? '14' : '34' }}分钟</div>
        </div>
        <div class="cancel-queue">
          <img src="../assets/cancel-icon.png" class="info-icon1" />
          <div class="cancel-text">若您行程有变，可<span class="highlight-cancel">取消本次排队</span></div>
        </div>
        <div class="distance-info">
          您目前与我们相距：<span class="distance-value">{{ examType === '内科检查' ? '672' : '864' }}m</span>
        </div>
      </div>
    </div>
      <!-- 其他内容保持不变 -->
 
    
    <div v-else-if="status === 'called'" class="called-info">
      <div class="already-called">已轮到您</div>
      <div class="go-to-room">前往<span class="highlight-text">{{ examType === '内科检查' ? '10' : '3' }}</span>诊室检查！</div>
      <div class="date-time-called">3月19日 星期三 {{ examType === '内科检查' ? '10:26:22' : '10:46:22' }}</div>
      <div class="confirm-button" @click="goToMedicalReminder">
        确定
      </div>
    </div>

    
    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item" @click="goToHomePage">
        <div class="nav-icon">
          <img src="../assets/home-icon1.png" alt="首页" class="nav-icon-img" />
        </div>
        <div class="nav-text">首页</div>
      </div>
      <div class="nav-item">
        <div class="nav-icon">
          <img src="../assets/my-icon1.png" alt="我的" class="nav-icon-img" />
        </div>
        <div class="nav-text1">我的</div>
      </div>
    </div>

    <!-- 语音按钮 -->
    <div class="voice-button">
      <img src="../assets/mic-icon.png" alt="语音" class="voice-icon-img" />
    </div>
  </div>

</template>

<script>
export default {
  name: 'QueueProgress',
  data() {
    return {
      examType: '',
      status: 'waiting', // 'waiting' 或 'called'
      currentTime: '',
      previouslyCompletedExam: '' // 添加这个属性来存储之前完成的检查
    }
  },
  created() {
    // 获取检查类型
    this.examType = this.$route.query.examType || localStorage.getItem('currentExamType') || '';
    // 获取状态
    this.status = this.$route.query.status || 'waiting';
    
    // 获取之前完成的检查
    this.previouslyCompletedExam = localStorage.getItem('previouslyCompletedExam') || '';
    
    // 设置当前时间
    this.updateCurrentTime();
    
    // 如果状态是waiting，几秒后自动切换到called状态
    if (this.status === 'waiting') {
      setTimeout(() => {
        this.status = 'called';
        // 更新时间为不同的值
        if (this.examType === '内科检查') {
          this.currentTime = '10:26:22';
        } else {
          this.currentTime = '10:46:22';
        }
      }, 1000); // 1秒后切换状态
    } else {
      // 如果已经是called状态，设置相应的时间
      if (this.examType === '内科检查') {
        this.currentTime = '10:26:22';
      } else {
        this.currentTime = '10:46:22';
      }
    }
  },
  methods: {
    // 获取进度条图片
    getProgressImage() {
      // 检查另一个检查是否已完成
      const otherExamCompleted = this.previouslyCompletedExam && this.previouslyCompletedExam !== this.examType;
      
      if (otherExamCompleted) {
        // 如果另一个检查已完成
        return this.status === 'waiting' ? 'load11.png' : 'load12.png';
      } else {
        // 如果另一个检查没完成
        return this.status === 'waiting' ? 'load9.png' : 'load10.png';
      }
    },
    
    goBack() {
      this.$router.go(-1);
    },
    
    goToHomePage() {
      // 不设置 allExamsCompleted 标记，直接跳转到首页
      // 这样即使用户点击底部导航栏的首页，也会显示原始的"去挂号"首页
      this.$router.push('/home');
    },
    
    goToMedicalReminder() {
      // 存储当前完成的检查类型
      localStorage.setItem('completedExam', this.examType);
      
      // 获取另一个检查类型
      // eslint-disable-next-line no-unused-vars
      const otherExam = this.examType === '内科检查' ? '心电图' : '内科检查';
      
      // 检查另一个检查是否已完成
      const previouslyCompletedExam = localStorage.getItem('previouslyCompletedExam');
      
      // 只有当之前已经完成了另一个检查，并且当前完成的检查与之前完成的不同，才认为所有检查都完成了
      if (previouslyCompletedExam && previouslyCompletedExam !== this.examType) {
        // 设置标记，表示所有检查已完成
        localStorage.setItem('allExamsCompleted', 'true');
        // 跳转到首页（此时首页会显示"查报告"和"去复诊"）
        this.$router.push('/home');
      } else {
        // 将当前完成的检查存储为之前完成的检查
        localStorage.setItem('previouslyCompletedExam', this.examType);
        
        // 如果另一个检查未完成，跳转回就诊提醒页面
        this.$router.push({
          path: '/medical-reminder',
          query: { 
            fromPaymentSuccess: 'true',
            completedExam: this.examType 
          }
        });
      }
    },
    
    updateCurrentTime() {
      // 根据检查类型设置不同的初始时间
      if (this.status === 'waiting') {
        this.currentTime = '10:12:22';
      } else {
        if (this.examType === '内科检查') {
          this.currentTime = '10:26:22';
        } else {
          this.currentTime = '10:46:22';
        }
      }
    }
  }
}
</script>

